export default {
	props: {
		item: Object,
		index: {
			type: Number,
			default: -1
		}, //listAll[listAllIndex].listItem[index]
		isDetail: { //是否处于详情页
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
		}
	},
	methods: {
		// 打开个人空间
		openSpace(){
			//处于详情页
			if(this.isDetail) return
			console.log('打开个人空间');
		},
		//关注
		follow(){
			//通知父组件更新follow状态
			this.$emit('follow', this.index);
		},
		//进入详情
		openDetail(){
			//处于详情页
			if(this.isDetail) return
			uni.navigateTo({
				url: '../../pages/detail/detail?detail=' + JSON.stringify(this.item)
			})
		},
		//顶踩操作
		doSupport(type){
			this.$emit('doSupport', {
				type: type,
				i: this.i,
				index: this.index
			})
		},
		// 评论
		doComment(){
			if(!this.isDetail) return this.openDetail();
			this.$emit('doComment')
		},
		//分享
		doShare(){
			if(!this.isDetail) return this.openDetail();
			this.$emit('doShare');
		}
	}
}
